import React from 'react';
import { BtnView } from 'components/common/button';

export const getColumConfig = that => (
  [
    {
      title: 'Mã SV',
      dataIndex: 'studentCode',
      key: 'studentCode',
      align: 'center'
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
      align: 'center'
    },
    {
      title: 'Thiếu thừa kỳ trước',
      dataIndex: 'previousAmt',
      key: 'previousAmt',
      align: 'center'
    },
  
    {
      title: 'Phải nộp kỳ',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    },
    {
      title: 'Đã nộp kỳ',
      dataIndex: 'paidAmt',
      key: 'paidAmt',
      align: 'center'
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      className: 'action-column',
      align: 'center',
      render: () => (
        <BtnView onClick={that.onViewRegistedCredits} />
      ),
    },
  ]
)


